import React, { useState }  from "react";
import { Link, Element , Events, animateScroll as scroll, scrollSpy, scroller } from 'react-scroll';
import '../node_modules/font-awesome/css/font-awesome.css';
import {AnimatePresence, motion} from "framer-motion";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faSass, faHtml5, faJs, faBootstrap, faReact } from '@fortawesome/free-brands-svg-icons'
import { faEye, faGamepad } from '@fortawesome/free-solid-svg-icons';


function PortfolioPage() {
    
  const pageTransition = {
    in: {
      translateX: 0,
      opacity: 1
    },

    out: {
      translateX: "-100vh",
      opacity: 0.2
    }
  };

  const [isActive,setActive] = useState("")

    return (
      <motion.div
        exit="out"
        animate="in"
        initial="out"
        variants={pageTransition}
      >
        <section data-index="1" className="portfolio-page" id="portfolio">
          <h1 className="portfolio-page__portfolio-title"><i className="fa fa-code"></i></h1>

  

          <div className="portfolio-gallery">

            <div className="portfolio-gallery__box">
            <img src={process.env.PUBLIC_URL + '/portfolio/NatoursGif.gif'}></img>
              <div
                className="portfolio-gallery__box__info"
                onClick={() => setActive("1")}
              >
                <h1>Advanced Sass Course - Natours</h1>
                <p>This is an online course from Udemy that taught me how use Sass to create sleek mondern websites.
                {"\n"} It also taught me a number of advanced techniques that showed me how much you can really do with just HTML/CSS, such as the checkbox hack!
                </p>
                <div className="portfolio-gallery__box__info__language-icons">
                <p><FontAwesomeIcon icon={faSass} size="3x"/></p>
                <p><FontAwesomeIcon icon={faHtml5} size="3x"/></p>
                </div>
                <a href="https://github.com/lewisd1996/PortfolioNatours/tree/master/after-S05"><button className="btn-view-code"><FontAwesomeIcon icon={faEye} size="3x"/>View Code</button></a>
              </div>
            </div>

            <div className="portfolio-gallery__box">
            <img src={process.env.PUBLIC_URL + '/portfolio/PokedexGif.gif'}></img>
              <div
                className="portfolio-gallery__box__info"
                onClick={() => setActive("1")}
              >
                <h1>Pokédex</h1>
                <p>This is Pokédex made with Bootstrap in ReactJS.
                {"\n"} It uses axios to pull data from the PokéAPI to allow users to view any Pokémon's data.
                </p>
                <div className="portfolio-gallery__box__info__language-icons">
                <p><FontAwesomeIcon icon={faBootstrap} size="3x"/></p>
                <p><FontAwesomeIcon icon={faReact} size="3x"/></p>
                <p><FontAwesomeIcon icon={faHtml5} size="3x"/></p>
                </div>

                <div className="portfolio-gallery__box__info__buttons">
                <a href="https://github.com/lewisd1996/PortfolioPokedex"><button className="btn-view-code"><FontAwesomeIcon icon={faEye} size="3x"/>View Code</button></a>
                <a href="https://lewisd1996.github.io/PortfolioPokedex/#/"><button className="btn-view-demo"><FontAwesomeIcon icon={faGamepad} size="3x"/>View Demo</button></a>
                </div>
              </div>
            </div>
            
          </div>

        </section>
      </motion.div>
    );
}

export default PortfolioPage;