import React from "react";
import { Link, Element , Events, animateScroll as scroll, scrollSpy, scroller } from 'react-scroll';
import '../node_modules/font-awesome/css/font-awesome.css';
import '../node_modules/font-awesome/css/font-awesome.min.css';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import {AnimatePresence, motion} from "framer-motion";

function CVPage() {
    
  const pageTransition = {
    in: {
      translateY: 0,
      opacity: 1
    },

    out: {
      translateY: "-100vh",
      opacity: 0.2
    }
  };

    return (    


      <motion.div 
      exit="out"
      animate="in"
      initial="out"
      variants={pageTransition} 
      >
      <section data-index="4" className="cv-page" id="cv">
      <h1 className="doodles-page__doodles-title"><i className="fa fa-file-text"></i></h1>
      
          <div className="cv-page__container">
          <div className="cv-page__container__download-box">
          <a className="cv-page__container__download-box__download-link__text">Thank you for having an interest in seeing my CV.{"\n"}{"\n"}Please click below to download!</a>
          <a className="cv-page__container__download-box__download-link" href={process.env.PUBLIC_URL + '/LewisDonleyCv.pdf'} download><i className="fa fa-cloud-download fa-5x"></i></a>
          
          </div>
          </div>
      </section>
      </motion.div>
    );
}

export default CVPage;